<template>
    <section>
        <tg-list v-show="IsMapView" :IsShowAction="true" ModuleName="FSM" SubModuleCode="FSM_MapExtend" :LegendArray="[]"
            :widgets="[]" :RenderRowActionMethod="renderActions" IdentityColumn="ID" :HeaderText="$t('MapExtend')"
            :listType="['List']" :ListData="mapList" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :ListDataCallBackFunction="fetchMapConfigList"
            v-on:ActionButtonClick="actionButtonClick" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick"> <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <listing-status :isfor="'MapExtend'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')"
                        ref="updatestatus" v-bind:dataid="data.row.ID" data-controller="FSM"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.STATUS_ID" data-action="index"
                        v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'" v-bind:callbackfunction="fetchMapConfigList"
                        v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.status_id, 'Ticketing')">
                        <span v-bind:class="getStatusClass( data.row.status_id, 'status_id')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                </template>
        </tg-list>
        <modalAddMapExtend v-if="showMapExtendPopup" mapContainer="MapExtend" mapType="Google" @Close="Close" @fetchMapConfigList="fetchMapConfigList" :mapObj="mapObj" :mapExtendId="mapExtendId"></modalAddMapExtend>
    </section>
</template>
<script>
import Schema from "@/views/MapExtend/MapExtendSchema";
import DataServices from "@/services/DataService";
import modalAddMapExtend from "../../components/Modal/modalAddMapExtend.vue";
export default {
    components:{
        modalAddMapExtend
    },
    data() {
        return {
            showMapExtendPopup:false,
            MapId: null,
            companyId: null,
            userId: null,
            Headers: [{
                "COLUMN_NAME": "CITY_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created at",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER ": 3,
            }],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            searchKeyWord: '',
            mapList: [],
            ItemObject: null,
            showaddNewModal: false,
            noRecord: false,
            latVal: '',
            lngVal: '',
            mapboxApi: '',
            mapType: '',
            modelType: '',
            modelItem: '',
            mapBoxName: '',
            mapLatLng: '',
            listActions: Schema.listActions,
            IsMapView: true,
            listheaderbuttons: Schema.buttons,
            mapObj:[],
            mapExtendId:null,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
        }
    },

    created: function () {
        this.fetchMapConfigList();
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.fetchMapConfigList();
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.fetchMapConfigList();
        },

        Searchdata: function () {
            var vm = this;
            //do something
            vm.SearchCondition = vm.SearchCondition;
            vm.fetchMapConfigList();
        },
        actionButtonClick(callbackfunction, item, index, event) {
            debugger
            switch (callbackfunction) {
                case 'Edit':
                    this.Edit(item)
                    break;
            }
        },
        Edit:function(item){
           // debugger
            var vm=this;
            vm.mapExtendId=null;
            vm.mapExtendId=item.ID;
            vm.mapObj=[];
            var obj = {
                refValue:  '',
                popupHeaderHtml: '',
                popupHeaderColor: "",
                popupBodyHtml: '',
                popupFooterHtml: '',
                pinLatLong: {
                    latitude: "",
                    longitude: ""
                },
                polygons: [],
                address: "",
                priority:'',
                color_code:''
            } 
            let latlng = [];
            let PolygonArray=[];
            let objLatLng=JSON.parse(item.LAT_LNG);
            objLatLng.forEach(function(row,i){
                latlng.push({
                    latitude:row[0],
                    longitude:row[1]
                })
            })
            PolygonArray.push({latLong:latlng});
            obj.polygons=PolygonArray;
            vm.mapObj.push(obj);
            vm.showMapExtendPopup=true;
        },
        openaddMap:function(){
            var vm=this;
            vm.mapExtendId=null;
            vm.mapObj=[];
            vm.showMapExtendPopup=true;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'ACTIVE':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'INACTIVE':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        changeMapStatus: function (MapId, status) {
            showLoader();
            if (status == 1001) {
                status = 'ACTIVE'
            }
            else {
                status = "INACTIVE";
            }
            var vm = this;
            var VarObject = {
                ID: MapId,
                Latitude: vm.latVal,
                Longitude: vm.lngVal,
                CityName: vm.mapType == 'Google' ? document.getElementById("autocomplete").value : document.getElementById("mapBoxName").value,
                StatusForUpdate: status,
                QUERY_TYPE: 'UPDSTS',
                Map_Type: vm.mapType,
                Map_Lat_lng: vm.mapLatLng
            }
            var objectPostString = JSON.stringify(VarObject)
            DataServices.SaveMapCityLatLng(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (JSON.parse(response.bodyText).status === 'Success') {
                        vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success", true, vm.$t('Alert'));
                        vm.fetchMapConfigList();
                    } else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }

            });

        },
        DeleteMultiple: function () {
            var controllerVal = "FSM";
            var actionVal = "DeleteMapExtend";
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Map")), function () {

                    var postJSON = {
                        ids: val,
                        moduleName: "FSM",
                        subModuleCode: "FSM_MAP"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityDataFSM(objectPostString).then(function (response) {
                        if (response.data.status == "Success") {                           
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Map')]), "Success", true, vm.$t('Alert'));
                            vm.fetchMapConfigList();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Map')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
        },
        SaveMapCityLocation: function () {
            // debugger;
            var vm = this;
            vm.mapBoxName = document.getElementById("mapBoxName").value;
            var checkForName = vm.mapType == 'Google' ? document.getElementById("autocomplete").value : vm.mapType == 'LeafLet' ? document.getElementById("searchtext9").value : vm.mapType == 'Esri' ? document.getElementById("searchtext9").value : vm.mapBoxName;
            if (checkForName.trim() == "") {
                ShowAlert(String.Format(Globalize.localize('SelectLocation'), Globalize.localize('Map')), "warning", true, Globalize.localize('Alert'), true);
                return false;
            }
            else { $('#divmapcitysave a').addClass('disabled'); }
            vm.mapLatLng = document.getElementById("mapLatLng").value;
            vm.latVal = document.getElementById("latval").value;
            vm.lngVal = document.getElementById("lngval").value;
            if (vm.latVal == '' || vm.lngVal == '' || vm.mapLatLng == '') {
                ShowAlert(String.Format("Please draw a polygon", Globalize.localize('Map')), "warning", true, Globalize.localize('Alert'), true);
                return false;
            }
            var data = {
                ID: vm.modelType == 'Add' ? null : vm.modelItem.ID,
                Latitude: vm.latVal,
                Longitude: vm.lngVal,
                CityName: vm.mapType == 'Google' ? document.getElementById("autocomplete").value : vm.mapType == 'LeafLet' ? document.getElementById("searchtext9").value : vm.mapType == 'Esri' ? document.getElementById("searchtext9").value : vm.mapBoxName,
                StatusForUpdate: '',
                QUERY_TYPE: vm.modelType == 'Add' ? 'INS' : 'UPD',
                Map_Type: vm.mapType,
                Map_Lat_lng: vm.mapLatLng
            };
            //debugger;
            var objectPostString = JSON.stringify(data)
            DataServices.SaveMapCityLatLng(objectPostString).then(response => {
                if (JSON.parse(response.bodyText).status == "Success") {
                    vm.ShowAlert(vm.$t('DataSavedContextMenu'), "success", true, vm.$t("Alert"));
                    if (vm.mapType == "Google") {
                        $('#googleMap').dialog("close");
                    }
                    else if (vm.mapType == "LeafLet" || vm.mapType == "Esri") {
                        $('#leaflet').dialog("close");
                    }
                    else {
                        $('#mapBox').dialog("close");
                    }
                    vm.fetchMapConfigList();
                }
            });
        },
        fetchMapConfigList: function () {
            debugger
            var vueObj = this;
            var url = `SortBy=${vueObj.SortBy}&SortExp=${vueObj.SortExp}&PageSize=${vueObj.PageSize}&PageNumber=${vueObj.PageNumber}`;
            DataServices.GetMapCityListByUserId(url).then(function (response) {
                 vueObj.TotalRecords = 0;
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "STATUS_ID") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.mapList = [];
                    if (response.data != "" && response.data != null) {
                    if (response.data.status == "Success") {
                        if (response.data.result.DATA.length > 0) {
                            vueObj.mapList = response.data.result.DATA;
                            vueObj.mapType = response.data.result.DATA[0].MAP_TYPE
                            vueObj.TotalRecords = vueObj.mapList[0].TOTALRECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.listheaderbuttons[1].isdisabled = true;
                            vueObj.noRecord = false;
                        }
                    }
                            else {
                                vueObj.noRecord = true;
                                vueObj.mapList = [];
                                vueObj.TotalRecords = 0
                            }
                        }
                        else {
                            vueObj.noRecord = true;
                            vueObj.mapList = [];
                            vueObj.Headers = [];
                            vueObj.TotalRecords = 0
                        }                    
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                });
        },
        Close:function(){
            var vm=this;
            vm.showMapExtendPopup=false;
        },
        commonHeaderButtonClick: function (event) {
            debugger
            switch (event.callbackfunction) {
                case "openaddTicket":
                    this.openaddTicket();
                    break;

                case "commonDeleteData":
                    this.DeleteMultiple();
                    break;
                case 'openaddMap':
                    this.openaddMap();
                    break;
            }
        },

    }

}
</script>