<template>
    <div>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full">
                            <h4 class="modal-title">{{ mapType }}</h4>
                            <button type="button" class="close" v-on:click="$emit('Close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <MapComponent :mapType="mapType" mapContainer="mapExtend" :Data="mapObj" :config="config" @SaveMapExtent="SaveMapExtent"></MapComponent>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </template>
    <script>
    import DataServices from "@/services/DataService";
    import MapComponent from "../Common/MapComponent.vue";
    export default {
        props:{
            mapObj:{
                required:false
            },
            mapExtendId:{
                required:false
            },
            mapType:{
                required:false
            }
        },
      components:{
        MapComponent
      },
      data(){
        return{
            mapLatLng:[],
            config: {
                isShowDrawButton: true,
                isShowEtaMap: false,
                isShowLocator:false,
                isEditable:true,
                isShowSaveExtentButton:true,
                isShowSearchBox:true
            },
        }
      },
      methods:{
        SaveMapExtent:function(data,mapType){
            var vm=this;
            if(data.length==0){
                vm.ShowAlert("Please select location", "warning");
                return false;
            }
            var CityName=data[0].CompleteAddress.FullAddress;
            var latVal=data[0].LatLng[0].Lat==undefined?data[0].LatLng[0].latitude:data[0].LatLng[0].Lat;
            var lngVal=data[0].LatLng[0].Lng==undefined?data[0].LatLng[0].longitude:data[0].LatLng[0].Lng;
            if(mapType=='Google')
            {
                if(data[0].LatLng[0].Lat!=undefined){
                    data[0].LatLng.forEach(function (item,i) {
                        vm.mapLatLng.push([item.Lng,item.Lat])
                    });
                    vm.mapLatLng.push([latVal,lngVal]);
                    }
                    else{
                        data[0].LatLng.forEach(function (item,i) {
                            vm.mapLatLng.push([item.longitude,item.latitude])
                        });
                    vm.mapLatLng.push([lngVal,latVal]);
                }
            }
            else{
                if(data[0].LatLng[0].Lat!=undefined){
                    data[0].LatLng.forEach(function (item,i) {
                        vm.mapLatLng.push([item.Lat,item.Lng])
                    });
                    vm.mapLatLng.push([latVal,lngVal]);
                    }
                    else{
                        data[0].LatLng.forEach(function (item,i) {
                            vm.mapLatLng.push([item.latitude,item.longitude])
                        });
                    vm.mapLatLng.push([latVal,lngVal]);
                }
            }
            
          
            var data = {
                ID: vm.mapExtendId==null?0:vm.mapExtendId,
                Latitude: latVal,
                Longitude: lngVal,
                CityName: CityName,
                StatusForUpdate: '',
                QUERY_TYPE:  vm.mapExtendId==null?'INS':'UPD',
                Map_Type: 'Google',
                Map_Lat_lng: JSON.stringify(vm.mapLatLng)
            };
            var objectPostString = JSON.stringify(data);
            DataServices.SaveMapCityLatLng(objectPostString).then(function (response) {
                if(response.data.status=="Success")
                {
                    vm.ShowAlert(vm.$t("DataSavedContextMenu"),"success");
                    vm.$emit('Close');
                    vm.$emit('fetchMapConfigList');
                }
            })
        }
      }
    }
    </script>
    